
import { defineComponent } from "vue";
import PickupIcon from "./PickupIcon.vue";

export default defineComponent({
  name: "PickupRecipe",
  components: {
    PickupIcon,
  },
  props: {
    idList: Array,
  },
  data() {
    return {}
  },

  computed: {
    ids() {
      let ret: number[] = []
      for (let i of this.idList ?? []) {
        ret.push(Number(i));
      }
      while (ret.length < 8) {
        ret.push(0);
      }
      return ret;
    }
  },
});
